.conatainer {
  background-color: blueviolet;
}

.containerHeader {
  margin: auto 200px;
}

.logo {
  text-align: center;
}

.search {
  display: flex;
  margin: auto;
}

.categories {
  margin: auto;
  display: flex;
  justify-content: center;
  gap: 150px;
}

.categor {
  cursor: pointer;
  position: relative;
}

.catSel {
  background: white;
  padding: 10px 10px;
  border-radius: 10px;
}

.sortProp {
  position: absolute;
  right: -10px;
  margin-top: -8px;
  background: #ffffff;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09);
  border-radius: 10px;
  overflow: hidden;
  z-index: 100;

  ul {
    overflow: hidden;
    padding: 0;
    text-align: center;

    li {
      list-style-type: none;
      padding: 12px 20px;
      cursor: pointer;

      &.active,
      &:hover {
        background: rgba(205, 30, 254, 0.52);
      }

      &.active {
        font-weight: bold;
        color: #fe5f1e;
      }
    }
  }
}