.root{
    display: flex;
    justify-content: center;
    position: relative;
}
.input{
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 12px 20px;
    width: 400px;
    border-radius: 12px;
    font-size: 16px;
    padding-left: 35px;
    transition: all 0.15s ease-in-out;

    &:focus{
        border: 1px solid rgba(0, 0, 0, 0.25);
    }

}

.icon{
    opacity: 0.3;
    position: absolute;
    left: 12px;
    top: 15px;
}
.iconClear{
    opacity: 0.3;
    position: absolute;
    right: 12px;
    top: 15px;
    cursor: pointer;
    
    &:hover{
        opacity: 0.8;
    }
}