.conatainer {
  background-color: blueviolet;
}

.containerHeader {
  margin: auto 200px;
  @media (max-width: 850px) {
    margin: auto 150px;
  }
  @media (max-width: 700px) {
    margin: auto 130px;
  }
  @media (max-width: 630px) {
    margin: auto 125px;
  }
   @media (max-width: 560px) {
    margin: auto 120px;
  }
  @media (max-width: 560px) {
    margin: auto 100px;
  }
  @media (max-width: 390px) {
    margin: auto 80px;
  }
  @media (max-width: 355px) {
    margin: auto 60px;
  }
  @media (max-width: 325px) {
    margin: auto 40px;
  }
}

.logo {
  text-align: center;
  padding-top: 20px;
  color: white;
  font-size: 35px;
  cursor: pointer;

}
 .logo:hover{
    opacity: 0.8;
  }

.search {
  display: flex;
  margin: auto;
  padding: 12px 14px;
  width: 340px;
  border-radius: 10px;
  border: none;
}

.categories {
  align-items: center;
  margin: auto;
  display: flex;
  justify-content: center;
  gap: 60px;
}

.categor {
  cursor: pointer;
  position: relative;
}

.catSel {
  background: white;
  padding: 10px 10px;
  border-radius: 10px;
}

.sortProp {
  position: absolute;
  right: -10px;
  margin-top: -8px;
  background: #ffffff;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09);
  border-radius: 10px;
  overflow: hidden;

  width: 140px;
  z-index: 100;

  ul {
    overflow: hidden;
    padding: 0;
    text-align: center;

    li {
      list-style-type: none;
      padding: 12px 20px;
      cursor: pointer;

      &.active,
      &:hover {
        background: rgba(205, 30, 254, 0.52);
      }

      &.active {
        font-weight: bold;
        color: #fe5f1e;
      }
    }
  }
}