.conatainerBookFull {
    background-color: rgba(255, 255, 255, 0.8);
    margin: 50px 135px;

    @media (max-width: 1300px) {
        margin: 50px 125px;
      }
      @media (max-width: 1200px) {
        margin: 50px 80px;
      }
      @media (max-width: 1100px) {
        margin: 50px 50px;
      }
      @media (max-width: 1000px) {
        margin: 50px 25px;
      }
      @media (max-width: 950px) {
        margin: 50px 15%;
      }
      @media (max-width: 400px) {
        margin: 50px 10%;
      }


    .bookFullCard {
        display: flex;
        justify-content: center;
        gap: 60px;

        @media (max-width: 950px) {
            display: flex;
            justify-content: center;
            flex-direction: column;
            gap: 20px;
          }

        .photoBookFullCard {
            background-color: white;
            border: none;
            padding: 25px;
            border-radius: 55px;
            height: 1%;
            -webkit-box-shadow: 0px -2px 65px -15px rgba(34, 60, 80, 0.31);
            -moz-box-shadow: 0px -2px 65px -15px rgba(34, 60, 80, 0.31);
            box-shadow: 0px -2px 65px -15px rgba(34, 60, 80, 0.31);

            

            img{
                width: 500px;
                @media (max-width: 950px) {
                    width: 100%;
                  }
            }
        }

        .textDescription{
            .category{
                color: #7b7474e3;
            }

            h4{
                color: #7b7474e3;
            }

            .desc{
            background-color: white;
            border: none;
            padding: 25px;
            border-radius: 15px;
            -webkit-box-shadow: 0px -2px 65px -15px rgba(34, 60, 80, 0.31);
            -moz-box-shadow: 0px -2px 65px -15px rgba(34, 60, 80, 0.31);
            box-shadow: 0px -2px 65px -15px rgba(34, 60, 80, 0.31);
            }
        }
    }
}