.container {
  background-color:unset;
  margin: auto 100px;
  @media (max-width: 740px) {
    margin: auto 50px;
  }
  @media (max-width: 650px) {
    margin: auto 20px;
  }
  @media (max-width: 580px) {
    margin: auto 10px;
  }
  @media (max-width: 560px) {
    display: block;
    margin: auto 25%;
  }
  @media (max-width: 410px) {
    display: block;
    margin: auto 23%;

  }
  @media (max-width: 360px) {
    display: block;
    margin: auto 20%;
  }
  @media (max-width: 320px) {
    display: block;
    margin: auto 19%;

  }
}

.containerBook {
  .loading{
    display: flex;
    justify-content: center;
  }
}

