.count{
    text-align: center;
    font-size: 20px;
    font-weight: 500;
  }
.container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  @media (max-width: 740px) {
    
  }
  .containerBookItem:hover {
    transform: scale(0.9);
    background-color: rgba(255, 255, 255, 0.8);
    cursor: pointer;
  }

  .containerBookItem {
    transition: .5s;

    .bookCardItem {
      margin-top: 25px;
      background-color: white;
      border: none;
      border-radius: 35px;
      -webkit-box-shadow: 0px -2px 65px -15px rgba(34, 60, 80, 0.31);
      -moz-box-shadow: 0px -2px 65px -15px rgba(34, 60, 80, 0.31);
      box-shadow: 0px -2px 65px -15px rgba(34, 60, 80, 0.31);

      .bookImg {
        padding: 10px;
        width: 250px;
      }

      .bookDescription {
        padding-left: 20px;

        .bookType {
          margin: 0px;
          color: #7b7474e3;
        }

        .bookTitle {
          margin: 0;
          padding-top: 10px;
        }

        .author {
          margin: 0;
          padding: 10px 0;
          color: #7b7474e3;

          .authorPI {
            margin: 0;
            padding: 0;
          }
        }
      }
    }

  }
}

.button {
  display: flex;
  justify-content: center;

  .btnHov{
    padding: 28px 15px;
    border:none;
    border-radius: 52px;
    font-size: 24px;
    margin-top: 15px;
    background-color: blueviolet;
    color: white;
    cursor: pointer;
  }
}

.btnHov:hover{
  opacity: 0.8;
    
}
